import React from 'react'
import {Link} from 'react-router'

export default class Main extends React.Component {

    render() {
        return(
            <div>
                <h1>
                    <div className="jumbotron">
                        <h1>!</h1>
                        <p>
                             -  .  <Link to="about"></Link>.   .    .
                        </p>
                        <p>
                            <div className="btn-group">
                                <Link to="login" className="btn btn-primary btn-lg"></Link>
                                <Link to="about" className="btn btn-default btn-lg"> ?</Link>
                            </div>
                        </p>
                    </div>
                </h1>
            </div>
        )
    }
}


