import React from 'react'
import {Link} from 'react-router'
import {connect} from 'react-redux'
import {log_out} from '../../actions/client'


@connect((store) => {
    return {
        client: store.reducer.client
    };
})



export default class Header extends React.Component {
    constructor(props) {
        super(props);
        this.onLogoutClick = this.onLogoutClick.bind(this);
    }
    onLogoutClick(e) {
        // console.info('Header onLogoutClick', this);
        this.props.dispatch(log_out('You are logged out'));
        this.props.router.push('login');
    }

    render() {
        const client = this.props.client;
        const info = this.props.client.client;

        let rocketbank_logo = 'data:image/png;base64,'

        let styles = {
            rocket_logo: {
                maxWidth: '19px'
            },
            nav: {
                marginTop: '25px'
            }
        }
        return(
            <nav className="navbar navbar-inverse" style={styles.nav}>
                <div className="container-fluid">
                    <div className="navbar-header">
                        <Link to="/" className="navbar-brand">
                            <img alt="" style={styles.rocket_logo} src={rocketbank_logo}/>
                        </Link>
                        <Link to="/" className="navbar-brand">
                            
                        </Link>
                    </div>
                    <div className="collapse navbar-collapse">
                        {
                            !(client.isAuthenticated) ? (
                                <div>
                                    <ul className="nav navbar-nav">
                                        <li>
                                            <a href="http://rocketbank.ru/loves/lol" target="_blank">   <span className="label label-danger">+500 </span></a>
                                        </li>
                                    </ul>
                                    <Link to="login" className="btn btn-default navbar-btn navbar-right"></Link>
                                </div>
                            ) : (
                                <div>
                                    <ul className="nav navbar-nav">
                                        <li><Link to="cards" activeClassName="active">, </Link></li>
                                        <li><Link to="support" activeClassName="active"> {
                                            (info['unread_messages'] && info['unread_messages'] > 0) ? (
                                                <span className="label label-success">{info['unread_messages']}</span>
                                            ) : null
                                        }</Link></li>
                                        <li><Link to="discounts" activeClassName="active"></Link></li>
                                    </ul>
                                    <Link to="profile" className="btn btn-default navbar-btn navbar-right"></Link>
                                </div>
                            )
                        }
                    </div>
                </div>
            </nav>
        )
    }
}


