import React from 'react'
import {
    get_currency_letter
} from './../../../helpers/currencies'

export default class Requisites extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        const acc = this.props.account
        const ad = acc.account_details

        let final_view = null

        if (acc.currency == "RUB") {
            final_view = (
                <div>
                    <b></b><br/> {ad.bank_name} <br/>
                    <b> </b><br/> {ad.ks} <br/>
                    <b> </b><br/> {ad.account} <br/>
                    <b></b><br/> {ad.owner} <br/>
                    <b> </b><br/> {ad.goal} <br/>
                    <b></b><br/> {ad.inn} <br/>
                    <b></b><br/> {ad.kpp}
                </div>
            )
        } else {
            final_view = (
                <div>
                    <b> </b><br/> {ad.corr} (SWIFT: {ad.corr_swift}) <br/>
                    <b> </b><br/> {ad.benef_bank} <br/>
                    <b>  </b><br/> {ad.benef_bank_address} <br/>
                    <b>SWIFT</b><br/> {ad.benef_swift} <br/>
                    <b></b><br/> {ad.owner} <br/>
                    <b> </b><br/> {ad.account}
                </div>
            )
        }

        return (<div>
            <div className="page-header">
                <h1>
                    
                </h1>
            </div>
            {final_view}
        </div>)
    }
}


